import React, { useEffect, useState } from "react";

export const HomeDark = () => {
  const [isVowel, setIsVowel] = useState(false);
  const [isShowArticle, setIsShowArticle] = useState(true);
  return (
    <section className="home-dark absolute inset-0 flex flex-col justify-center items-stretch  bg-gray-900 transition-all w-full overflow-hidden  z-10">
      <div className="select-none block text-gray-300 text-2xl sm:text-4xl text-center relative -top-5 ">
        <span className="flex justify-center">
          I'm {isShowArticle && "A" + (isVowel ? "n" : "")}
        </span>
        <TextScrollY vowel={setIsVowel} article={setIsShowArticle} />
      </div>
    </section>
  );
};

const TextScrollY = ({ vowel, article }) => {
  const [currId, setCurrId] = useState(0);
  const iAmA = [
    "Software Developer",
    "researcher",
    "Tech enthusiast",
    "Engineer",
    "Tinkerer",
    "Soumya Ranjan Patnaik",
  ];

  useEffect(() => {
    const intervalRef = setInterval(() => {
      setCurrId((prev) => {
        return (prev + 1) % iAmA.length;
      });
    }, 2250);

    return () => {
      clearInterval(intervalRef);
    };
  });

  useEffect(() => {
    if (currId === iAmA.length - 1) {
      article(false);
      return;
    }
    article(true);
    const curr = iAmA[currId];
    if (curr.match(`^[aeiouAEIOU].*`)) {
      vowel(true);
    } else {
      vowel(false);
    }
  }, [currId]);
  // Render the required elements
  const renderElements = iAmA.reduce((prev, curr, id) => {
    const len = iAmA.length;
    if ((currId + 1 + len) % len === id) {
      return [...prev, { desc: curr, type: "next" }];
    } else if (currId === id) {
      return [...prev, { desc: curr, type: "curr" }];
    } else {
      return [...prev, { desc: curr, type: "prev" }];
    }
  }, []);

  return (
    <div
      id="home"
      className="select-none relative flex justify-center h-11 sm:h-20 overflow-hidden w-full"
    >
      {renderElements.map((item, index) => {
        return (
          <h3
            className={`absolute text-4xl sm:text-6xl font-bold duration-300 transition-all capitalize ${item.type}-slider`}
            key={index}
          >
            {item.desc}
          </h3>
        );
      })}
    </div>
  );
};
export default HomeDark;
