import "./App.css";
import Navbar from "./Nav/Nav";
import { HomeDark } from "./Home/Home";
import About from "./About/About";
import { Route, useLocation } from "react-router-dom";
import links from "./Nav/links";
import { useEffect } from "react";

function App() {
  const location = useLocation();
  useEffect(() => {
    const curr_id = links.filter((item) => item.url === location.pathname).id;
    console.log(curr_id);
    document.getElementById(curr_id)?.scrollIntoView();
  }, [location]);
  return (
    <div className="h-screen overflow-y-auto overflow-x-hidden scrollbar scrollbar-thin scrollbar-track-transparent scrollbar-thumb-gray-700">
      <Navbar />
      <div className="relative  h-3/5 sm:h-3/4">
        <HomeDark />
      </div>
      <About />
    </div>
  );
}

export default App;
